import { createAction, props } from '@ngrx/store';

import { Loan } from '@backbase/loans-collection-ui-ang';

import { LoansResponse } from '../../models/loans-response.model';

// Fetch loans
export const loadLoans = createAction('[Loans API] fetch loans', props<{ page: number; append: boolean }>());
export const loadLoansSuccess = createAction(
  '[Loans API] fetch loans succeed',
  props<{ response: LoansResponse; append: boolean }>(),
);
export const loadLoansError = createAction('[Loans API] fetch loans error', props<{ error: string }>());

// Fetch loan by id
export const fetchLoanById = createAction('[Loans API] fetch loan by id', props<{ id: string }>());
export const fetchLoanByIdSuccess = createAction('[Loans API] fetch loan by id succeed', props<{ loan: Loan }>());
export const fetchLoanByIdError = createAction('[Loans API] fetch loan by id error', props<{ error: string }>());

// Fetch amount of loans
export const fetchAmountOfLoans = createAction('[Loans API] fetch amount of loans');
export const fetchAmountOfLoansSuccess = createAction(
  '[Loans API] fetch amount of loans succeed',
  props<{ totalCount: number }>(),
);
export const fetchAmountOfLoansError = createAction('[Loans API] fetch amount of loans error');

// Update loan alias
export const updateLoanAlias = createAction('[Product Summary API] Update alias', props<{ alias: string }>());
export const updateLoanAliasSuccess = createAction(
  '[Product Summary API] Update alias succeed',
  props<{ alias: string; id: string }>(),
);
export const updateLoanAliasError = createAction(
  '[Product Summary API] Update alias error',
  props<{ error: string }>(),
);
export const updateLoanAliasCancel = createAction('[Effect] Cancel update alias request');
